<template>
  <!--  v-if="adminUser !== null" -->
  <div>
    <!-- Header -->
    <notifications></notifications>
    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">
            TuneKey Dynamic Link
          </h6>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <base-button
            size="sm"
            type="neutral"
            @click.prevent="logout"
            class="tk-font-color-main"
            >Logout</base-button
          >
        </div>
      </div>
    </base-header>

    <div class="container-fluid mt--6">
      <div class="card mb-4">
        <div class="card-header" v-if="!isLoading">
          <div class="row align-items-center">
            <div class="col-8">
              <!-- <h5 class="h3 mb-0">Links</h5> -->
              <span>From</span>
              <flat-picker
                :config="{ allowInput: true }"
                class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px8 tk-px-height-px36"
                v-model="from"
              >
              </flat-picker>
              <span> - To</span>
              <flat-picker
                :config="{ allowInput: true }"
                class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px8 tk-px-height-px36"
                v-model="to"
              >
              </flat-picker>
              <span
                class="btn btn-sm btn-neutral tk-px-margin-left-px16"
                :class="{
                  'tk-bg-color-gray-400 tk-border-color-gray-400 tk-font-color-white': !applyIsEnable,
                  'tk-font-color-main': applyIsEnable,
                }"
                @click.prevent="initAnalytics"
                >Apply</span
              >
            </div>
            <div class="col-4 text-right">
              <span
                class="btn btn-sm btn-neutral tk-font-color-main"
                @click.prevent="showModal = true"
                >New Link</span
              >
            </div>

            <div style="margin-top: 10px; font-size: 14px" class="tk-dynamic-range-filter">
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 0,
                }"
                @click.prevent="range = 0"
                >Total</span
              >
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 1,
                }"
                @click.prevent="range = 1"
                >Last 7 days</span
              >
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 2,
                }"
                @click.prevent="range = 2"
                >Last 30 days</span
              >
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 3,
                }"
                @click.prevent="range = 3"
                >This week</span
              >
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 4,
                }"
                @click.prevent="range = 4"
                >This month</span
              >
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 5,
                }"
                @click.prevent="range = 5"
                >This quater</span
              >
              <span
                class="tk-px-margin-left-px16 tk-cursor-pointer"
                style="color: #8898aa"
                :class="{
                  'tk-font-color-main tk-font-weight-bold': range === 6,
                }"
                @click.prevent="range = 6"
                >This year</span
              >
            </div>
          </div>
        </div>
        <div class="card-body" style="height: calc(100vh - 220px)">
          <card-loading v-if="isLoading"></card-loading>
          <template v-else>
            <spread-dynamic-link-inline-action-table
              :salesManParams="salesManParams"
              :accessToken="accessToken"
              :from="from"
              :to="to"
              @analytic="analytic"
            ></spread-dynamic-link-inline-action-table>
          </template>
        </div>
      </div>
    </div>

    <modal :show.sync="showModal">
      <template slot="header">
        <h5 class="modal-title" id="exampleModalLabel">New Link</h5>
      </template>
      <div class="tk-popup-content" style="max-height: 60vh; overflow-y: auto">
        <!-- Name |  email(optional)  |  0-6 months for free | expiration date -->
        <div>
          <!-- name -->
          <span>
            <div class="form-group">
              <label class="form-control-label">Name</label>
              <div class="has-label">
                <input
                  type="text"
                  class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
                  v-model="name"
                />
              </div>
            </div>
          </span>

          <!-- email -->
          <div>
            <span>
              <div class="form-group">
                <label class="form-control-label">Email</label>
                <div class="has-label">
                  <input
                    type="email"
                    class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
                    :class="{ 'is-invalid': isInvalid }"
                    @blur.prevent="validationEmail"
                    v-model="email"
                  />
                </div>
                <div
                  class="invalid-feedback"
                  style="display: block"
                  v-show="isInvalid"
                >
                  The email is not correct.
                </div>
              </div>
            </span>
          </div>

          <!-- months -->
          <span>
            <div class="form-group tk-px-margin-bottom-px0">
              <label class="form-control-label">Free months</label>
              <div class="has-label">
                <el-select
                  v-model="months.selected"
                  class="tk-select-border-none tk-border-bottom tk-select-bg-mute"
                >
                  <el-option
                    v-for="(option, index) in months.options"
                    :key="index"
                    :label="
                      option > 0 ? option + '-MONTH-FREE' : 'FREE DOWNLOAD'
                    "
                    :value="option"
                  >
                  </el-option>
                </el-select>
              </div>
            </div>
          </span>
          <img
            :src="'/img/coupon/coupon-' + months.selected + '.png'"
            style="width: calc(100% - 4rem); display: block; margin: 0 auto"
          />

          <!-- expiration -->
          <span>
            <div class="form-group card-wrapper tk-px-margin-bottom-px0">
              <div class="card tk-px-margin-bottom-px16">
                <div class="form-control" style="height: auto">
                  <span
                    class="tk-layout-inline-block tk-font-color-black1"
                    style="width: calc(100% - 56px)"
                  >
                    <span v-if="!expirationOpen">Expiration</span>
                    <template v-else>
                      <span>Expiration date</span>
                      <flat-picker
                        :config="{ allowInput: true }"
                        class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px10 tk-px-height-px36"
                        v-model="expiration"
                      >
                      </flat-picker>
                    </template>
                  </span>
                  <base-switch
                    class="mr-1 tk-layout-inline-block"
                    v-model="expirationOpen"
                    on-text="On"
                    off-text="Off"
                    type="main"
                  ></base-switch>
                </div>
              </div>
            </div>
          </span>
        </div>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showModal = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <button-with-loading
          style="margin: 0 0 0 1rem"
          text="Create"
          @start="addNewLink"
          :type="'main'"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
        <!-- <base-button
          type="primary"
          @click.prevent="addNewLink"
          class="tk-font-color-white tk-bg-color-main tk-border-none"
          >Create</base-button
        > -->
      </template>
    </modal>
  </div>
</template>

<script>
import "flatpickr/dist/flatpickr.css";
import bus from "@/bus.js";
import { Select, Option } from "element-ui";
import collections from "@/services/collections";
export default {
  components: {
    CardLoading: () => import("@/tkViews/components/layout/CardLoading"),
    SpreadDynamicLinkInlineActionTable: () =>
      import("./RegularTables/SpreadDynamicLinkInlineActionTable"),
    flatPicker: () => import("vue-flatpickr-component"),
    [Select.name]: Select,
    [Option.name]: Option,
    buttonWithLoading: () =>
      import("@/tkViews/components/layout/ButtonWithLoading"),
  },
  data() {
    return {
      from: "",
      to: "",
      range: 1,
      // 0: total, 1: 7d, 2: 30d, 3: wk, 4: mo, 5:qr, 6: yr
      adminUser: null,
      isLoading: true,
      applyIsEnable: false,
      dynamicLinksData: [],
      showModal: false,
      isInvalid: false,
      expirationOpen: false,
      isButtonLoadingComplete: false,
      expiration: "",
      email: "",
      name: "",
      months: {
        selected: 3,
        options: [0, 1, 2, 3, 4, 5, 6],
      },
      originSalesManParams: [],
      salesManParams: [],
      salesMan: [],
      links: [],
      accessToken: "",
      json: {
        expireIn: 1614556800,
        nonce: "jAHkKLOg2kGvnqUv",
        status: 1,
        desc: "You are currently PRO now, enjoy TuneKey with unlimited access!",
        salesManId: "FHSpoJJKF",
        imageURL:
          "https://firebasestorage.googleapis.com/v0/b/tunekey-2019.appspot.com/o/images%2Fothers%2Fcoupon.png?alt=media&token=fd25342c-2377-48df-8cd1-3e2c225d6d9a",
        id: "JPSI82IK2",
        time: 3,
      },
    };
  },
  async created() {
    let self = this;
    let user = localStorage.getItem("adminUser");
    let info = await collections.config.doc("salesAccount").get();
    let sales = info.data().sales;

    self.initFromTo();

    if (self.$tools.isNull(user)) {
      self.$router.push({ name: "Login" });
    } else {
      self.adminUser = JSON.parse(user);

      for (let i = 0; i < sales.length; i++) {
        if (
          sales[i].account === self.adminUser.account &&
          sales[i].password === self.adminUser.password
        ) {
          self.getAccessToken();
          break;
        } else {
          if (i === sales.length - 1) {
            self.$router.push({ name: "Login" });
          }
        }
      }
    }
  },
  watch: {
    expirationOpen(newVal, oldVal) {
      if (newVal) {
        this.expiration = this.$tools.formatYYMMDD(new Date().getTime());
      } else {
        this.expiration = "";
      }
    },
    range(newVal, oldVal) {
      // let d = new Date(timestamp);
      // d.setDate(d.getDate() + 1);
      // d.setHours(0, 0, 0, 0);
      if (newVal > -1) {
        this.initFromTo();
      }
    },
    from(newVal, oldval) {
      this.applyIsEnable = true;
      if (newVal.indexOf("-") > -1) {
        let from = this.$tools.formatYYMMDD(new Date(newVal).getTime());
        if (new Date(from).getTime() > new Date().getTime()) {
          from = this.$tools.formatYYMMDD(new Date().getTime());
        }
        this.initRangeFromDay();
        this.from = from;
      }
    },
    to(newVal, oldval) {
      this.applyIsEnable = true;
      if (newVal.indexOf("-") > -1) {
        let to = this.$tools.formatYYMMDD(new Date(newVal).getTime());
        if (new Date(to).getTime() > new Date().getTime()) {
          to = this.$tools.formatYYMMDD(new Date().getTime());
        }
        this.initRangeFromDay();
        this.to = to;
      }
    },
  },
  methods: {
    analytic() {
      this.isLoading = false;
      console.log("分析完成");
    },
    initAnalytics() {
      let self = this;
      if (self.applyIsEnable) {
        self.applyIsEnable = false;
        bus.$emit("analytic");
      }
    },
    initRangeFromDay() {
      let days = this.$tools.getDaysByDateString(this.from, this.to);
      if (days <= 7) {
        if (
          this.to === this.$tools.formatYYMMDD(new Date().getTime()) &&
          this.$tools.datesIsSameWeek(
            new Date(this.from).getTime(),
            new Date(this.to).getTime()
          )
        ) {
          this.range = 3;
        } else {
          this.range = -1;
        }
      }
    },
    initDayFromRange() {
      let self = this;
      // 0: total, 1: 7d, 2: 30d, 3: wk, 4: mo, 5:qr, 6: yr
      let day = 0;
      let d = new Date();
      let wkDay = d.getDay();
      let nowYMD = self.$tools.formatYYMMDD(d.getTime());
      let startYMD;
      switch (self.range) {
        case 0:
          let links = self.links.sort((a, b) => {
            return parseInt(a.createTime) - parseInt(b.createTime);
          });
          startYMD = self.$tools.formatYYMMDD(links[0].createTime * 1000);
          day = self.$tools.getDaysByDateString(startYMD, self.to) - 1;
          break;
        case 1:
          day = 7;
          break;
        case 2:
          day = 30;
          break;
        case 3:
          day = wkDay === 0 ? 6 : wkDay - 1;
          break;
        case 4:
          day = d.getDate() - 1;
          break;
        case 5:
          startYMD = self.$tools.getQuarterStartDate();
          console.log("quarter start: ", startYMD);
          day = parseInt(
            (new Date(nowYMD).getTime() - new Date(startYMD).getTime()) /
              (24 * 60 * 60 * 1000)
          );
          break;
        case 6:
          day = parseInt(
            (new Date(nowYMD).getTime() -
              new Date(d.getFullYear() + "/1/1").getTime()) /
              (24 * 60 * 60 * 1000)
          );
          break;
      }
      return day;
    },
    async initFromTo() {
      let self = this;
      let day = await self.initDayFromRange();
      let dTo = new Date(),
        dFrom = new Date();
      dFrom.setDate(dFrom.getDate() - day);
      self.to = self.$tools.formatYYMMDD(dTo.getTime());
      self.from = self.$tools.formatYYMMDD(dFrom.getTime());
    },
    getAccessToken() {
      let self = this;
      let func = self.$firebase.functions().httpsCallable("getAccessToken");
      func().then((result) => {
        if (!self.$tools.isNull(result.data.data)) {
          self.accessToken = result.data.data.token;
        }
        self.getSalesMan();
      });
    },
    getSalesMan() {
      let self = this;
      collections.salesMan.onSnapshot((snapshots) => {
        if (snapshots.empty) {
          self.salesMan = [];
        } else {
          let data = [];

          snapshots.forEach((item) => {
            data.push(item.data());
          });

          self.salesMan = data;
        }
        console.log("listen salesMan: ", self.salesMan);
        self.getLinks();
      });
    },
    getLinks() {
      let self = this;
      // collections.salesManLinks.onSnapshot((snapshots) => {
      collections.salesManLinks.get().then((snapshots) => {
        if (snapshots.empty) {
          self.links = [];
        } else {
          let data = [];

          snapshots.forEach((item) => {
            if (item.data().creator === self.adminUser.account) {
              data.push(item.data());
            }
          });

          self.links = data;
        }
        self.getSalesManParams();
        console.log("links: ", self.links);
      });
    },
    getSalesManParams() {
      let self = this;
      // collections.salesManParams.onSnapshot((snapshots) => {
      collections.salesManParams.get().then((snapshots) => {
        if (snapshots.empty) {
          self.salesManParams = [];
        } else {
          let data = [];

          snapshots.forEach((item) => {
            let obj = self.$tools.objKeySort(item.data());
            obj.email = "";
            self.salesMan.some((i) => {
              if (i.salesManId == obj.salesManId) {
                obj.email = i.email;
              }
            });
            self.links.some((i) => {
              if (i.id == obj.id) {
                obj.link = i.link;
                obj.createTime = i.createTime;
                data.push(obj);
              }
            });
          });

          self.salesManParams = data;
        }
        console.log("data: ", self.salesManParams);
        self.originSalesManParams = self.salesManParams;
        self.isLoading = false;
        self.initDataByRange();
      });
    },
    initDataByRange() {
      let self = this;
    },
    logout() {
      let self = this;
      // bus.$emit("showLoading");
      localStorage.clear();
      self.$router.push({ name: "AdministrationLogin" });
      // bus.$emit("hideLoading");
    },
    async addNewLink() {
      let self = this;

      // salesManParams
      let dynamic = {
        expireIn: self.$tools.isNull(self.expiration)
          ? 0
          : parseInt(new Date(self.expiration).getTime() / 1000),
        nonce: self.$tools.getRandomString(16), // 16
        status: 1, // active: 1, archive: 0
        desc: "You are currently PRO now, enjoy TuneKey with unlimited access!",
        salesManId: self.$tools.getRandomString(9), // 9
        imageURL:
          "https://firebasestorage.googleapis.com/v0/b/tunekey-2019.appspot.com/o/images%2Fothers%2Fcoupon-" +
          self.months.selected +
          ".png?alt=media&token=fd25342c-2377-48df-8cd1-3e2c225d6d9a",
        id: self.$tools.getRandomString(9), // 9
        time: self.months.selected,
      };

      // 排序
      dynamic = this.$tools.objKeySort(dynamic);

      // 拼接
      let signature = this.$tools.getJsonKeyValueStr(dynamic);
      signature = signature.substring(0, signature.length - 1);

      // md5
      let md5 = String(this.$md5(signature)).toUpperCase();

      // 生成短链接
      await self.$axios
        .post(
          "https://firebasedynamiclinks.googleapis.com/v1/shortLinks?key=AIzaSyBdUyJmAAbAGEWq0hsbcidOUiaQU1aLxkM",
          {
            // longDynamicLink:
            //   "https://tunekey.app/link/?link=https://tunekey.app/redirect/i?u=" +
            //   dynamic.salesManId +
            //   "&c=" +
            //   dynamic.id +
            //   "&s=" +
            //   md5,
            suffix: {
              option: "SHORT",
            },
            dynamicLinkInfo: {
              domainUriPrefix: "https://tunekey.app/link",
              link:
                "https://tunekey.app/redirect/i?u=" +
                dynamic.salesManId +
                "&c=" +
                dynamic.id +
                "&s=" +
                md5 +
                "&t=" +
                new Date().getTime(),
              // link: "https://tunekey.app",
              androidInfo: {
                androidPackageName: "com.spelist.tunekey",
                androidFallbackLink: "https://tunekey.app/download",
                androidMinPackageVersionCode: "1",
              },
              iosInfo: {
                iosBundleId: "com.spelist.tunekey",
                iosIpadBundleId: "tunekey",
                iosAppStoreId: "1479006791",
              },
              navigationInfo: {
                enableForcedRedirect: false,
              },
              socialMetaTagInfo: {
                socialTitle: "",
                socialDescription: "",
                socialImageLink: dynamic.imageURL,
              },
            },
          }
        )
        .then(async function (res) {
          // console.log(res);

          if (res.data.shortLink) {
            // salesManParam
            let param = dynamic;
            param.name = self.name;
            await collections.salesManParams.doc(dynamic.id).set(param);

            // salesManLinks
            await collections.salesManLinks.doc(dynamic.id).set({
              link: res.data.shortLink,
              id: dynamic.id,
              createTime: parseInt(new Date().getTime() / 1000),
              creator: self.adminUser.account,
            });

            // salesMan
            await collections.salesMan.doc(dynamic.salesManId).set({
              salesManId: dynamic.salesManId,
              email: self.email,
            });

            // signUpViaLinkCount
            await collections.signUpViaLinkCount.doc(dynamic.id).set({
              count: 0,
              id: dynamic.id,
            });

            self.isButtonLoadingComplete = true;
            self.showModal = false;

            self.$notify({
              message: "Create Success!",
              type: "success",
            });
          }
        })
        .catch(function (err) {
          console.log(err);
        });

      self.name = "";
      self.email = "";
      self.isButtonLoadingComplete = false;
    },
    validationEmail() {
      let self = this;

      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
    getDynamicLinks(salesManId) {
      return new Promise((resolve, reject) => {
        db.collection("sales_man_params")
          .where("salesManId", "==", salesManId)
          .get()
          .then((snapshot) => {
            if (!snapshot.empty) {
              let data = [];
              snapshot.forEach((doc) => {
                data.push(doc.data());
              });
              resolve(data);
            } else {
              resolve([]);
            }
          })
          .catch((err) => {
            console.error("Get [sales_man_params] failed, error: ", err);
            resolve([]);
          });
      });
    },
    getSalesManId(userId) {
      return new Promise((resolve, reject) => {
        db.collection("sales_man_record")
          .where("userId", "==", userId)
          .get()
          .then((snapshot) => {
            if (!snapshot.empty) {
              let data = [];
              snapshot.forEach((doc) => {
                data.push(doc.data());
              });
              resolve(data[data.length - 1]);
            } else {
              resolve(null);
            }
          })
          .catch((err) => {
            console.error("Get [sales_man_record] failed, error: ", err);
            resolve(null);
          });
      });
    },
  },
};
</script>

<style scoped>
.tk-popup-content::-webkit-scrollbar {
  display: none;
}
.tk-dynamic-range-filter > span:hover {
  text-decoration: underline;
}
</style>